import { useState } from "react";
import FormCalendarInput from "../../../Ui/Input/FormCalendarInput";
import CustomSelect from "../../../Ui/Select/CustomSelect";
import FeeStatisticsBankFilter from "../FeeStatistics/FeeStatisticsBankFilter";
import {
  slashDateFormat,
  ymdSlashDateFormat,
} from "../../../Utils/Functions/dateUtility";
import { useTranslation } from "react-i18next";
import {
  existingRequiredField,
  requiredField,
} from "../../../Validation/functions/checkingRequiredField";
import style from "../FinanceManager.module.css";
import CreateButton from "../../../Ui/Button/CreateButton";
const FinanceDaybookFilter = ({ fid }) => {
  const { t } = useTranslation();
  const [state, setState] = useState({
    bank: "",
    from: "",
    to: "",
    payment_type: "",
    daybook_type: "",
  });
  const [disabled, setDisabled] = useState(false);
  const [errorField, setErrorField] = useState("");

  const onTransactionDate = (val, flow) => {
    setState((prev) => ({
      ...prev,
      [flow]: ymdSlashDateFormat(val),
    }));
  };
  const onSelectBank = (value) => {
    setState((pre) => ({
      ...pre,
      bank: value,
    }));
  };
  const onSelectType = (val) => {
    setState((prev) => ({
      ...prev,
      payment_type: val === "Total" ? "BOTH" : val,
    }));
  };
  const onSelectDaybookType = (val) => {
    setState((prev) => ({
      ...prev,
      daybook_type: val,
    }));
  };

  const onFilter = () => {
    let validation = requiredField(state, []);
    if (existingRequiredField(validation) && fid && state.bank?._id) {
      setDisabled((pre) => !pre);
      //   financeBankDaybook({
      //     fid: fid,
      //     bank: state.bank?._id,
      //     from: state.from,
      //     to: state.to,
      //     payment_type: state.payment_type,
      //   })
      //     .then(() => {
      //       setDisabled((pre) => !pre);
      //
      //     })
      //     .catch({});
    } else {
      setErrorField(validation);
    }
  };
  return (
    <>
      <div
        className={style.fm_header_start_container}
        style={{
          gap: "2%",
          flexWrap: "wrap",
        }}
      >
        <FeeStatisticsBankFilter
          fid={fid}
          onSelect={onSelectBank}
          errorShow={errorField["bank_account"]}
          viewAs="NORMAL_FILTER"
          defalutValue={state?.bank?.finance_bank_account_name ?? ""}
          customStyleContainer={{
            width: "49%",
          }}
          customListStyle={{
            position: "absolute",
            width: "100%",
            zIndex: "13",
            top: "5.4rem",
          }}
        />
        <CustomSelect
          selectLabel={t("daybook_type")}
          selectedValue={t("daybook_type_placeholder")}
          options={["Admission", "Miscellaneous"]}
          onClick={onSelectDaybookType}
          defalutValue={state.daybook_type}
          customStyleContainer={{
            width: "49%",
          }}
          customListStyle={{
            position: "absolute",
            width: "100%",
            zIndex: "13",
            top: "5.4rem",
          }}
        />

        <FormCalendarInput
          label={t("from")}
          placeholder={t("please_select_from_date")}
          name={"from"}
          value={state.from ? slashDateFormat(state.from) : ""}
          onChange={(e) => onTransactionDate(e, "from")}
          errorShow={errorField["from"]}
          customFormInputContainer={{
            width: "32%",
          }}
        />
        <FormCalendarInput
          label={t("to_only")}
          placeholder={t("please_select_to_date")}
          name={"to"}
          value={state.to ? slashDateFormat(state.to) : ""}
          onChange={(e) => onTransactionDate(e, "to")}
          errorShow={errorField["to"]}
          customFormInputContainer={{
            width: "32%",
          }}
        />
        <CustomSelect
          selectLabel={t("payment_type")}
          selectedValue={t("please_select_payment_type")}
          options={["Payment Gateway / Online", "By Cash", "Total"]}
          onClick={onSelectType}
          defalutValue={state.payment_type}
          customStyleContainer={{
            width: "32%",
          }}
          customListStyle={{
            position: "absolute",
            width: "100%",
            zIndex: "13",
            top: "5.4rem",
          }}
        />
      </div>
      <CreateButton label="view_report" onAction={onFilter} />
    </>
  );
};

export default FinanceDaybookFilter;
